<template>
  <div class="page content">
    <h1>   2009  {{new Date().getFullYear()}} </h1>
    <div class="ballList">
      <div class="ballList__line"></div>
      <div class="ball" v-for="(skill, index) in skills" :key="index" :class="{'ball-left': !(index % 2), 'ball-right': index % 2,}">
        <h3></h3>
        <div class="ball__skill">
          <img v-for="type in skill.skillType" :src="getType(type)" width="32" :title="type"/>
        </div>
        <h3> </h3>
        <p>{{skill.descWork}}</p>
        <h3></h3>
        <ul>
          <li v-for="(duty, index) in skill.duties" :key="index">
            {{duty}}
          </li>
        </ul>
        <div class="ball__year">{{skill.year}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Skills',
  data () {
    return {
      skills:[
        {
          skillType: ['html', 'css', 'jquery'],
          descWork: '   .       .   .      ,     Jquery      .    .',
          url:'',
          duties: ['  ', '      Jquery', ' '],
          year: 2009
        },
        {
          skillType: ['army'],
          descWork: '     :)',
          url:'',
          duties: [' :)'],
          year: 2012
        },
        {
          skillType: ['html', 'css', 'jquery', 'php', 'bitrix'],
          descWork: '           Bitrix.',
          url:null,
          duties: ['   Bitrix', ' ', '      Jquery'],
          year: 2013
        },
        {
          skillType: ['html', 'css', 'jquery', 'bootstrap', 'php', 'codeigniter'],
          descWork: '  Callback       ',
          url:'http://silvertelecom.net/',
          duties: ['    ', '    Callback ', ' ', '  Api '],
          year: 2014
        },
        {
          skillType: ['html', 'css', 'jquery', 'bootstrap', 'androidstudio'],
          descWork: '   Android     " ".    ""',
          url:'http://fest62.ru',
          duties: ['', ' Android '],
          year: 2015
        },
        {
          skillType: ['html', 'css', 'jquery', 'bootstrap', 'androidstudio', 'php', 'phalcon', 'laravel', 'js', 'vuejs'],
          descWork: '      backEnd, FrontEnd, MobileAndroid.     ,      FrontEnd .',
          url:'',
          duties: ['', ' Android ', '  Web      ', '  ,       '],
          year: 2015
        },
        {
          skillType: ['html', 'css', 'php', 'js', 'reactjs', 'vuejs'],
          descWork: '  FrontEnd.   VueJs.',
          url:'',
          duties: [' CRM, ECM  .. ', ' ', '  VueJS', '    ReactJS'],
          year: 2017
        }
      ]
    }
  },
  methods:{
    getType(type){
      return require(`@/assets/skill/${type}.png`);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .content{
    > h1{
      margin-bottom: 8px;
      font-size: calc(1em + 1vw);
    }
  }
  .ballList{
    position: relative;
    display: flex;
    flex-direction: column;
    &__line{
      position: absolute;
      border: 1px solid #2c98f0;
      height: 100%;
      margin-left: 50%;
    }
  }
  .ball{
    width: calc(50% - 28px);
    border: 2px solid #2c98f0;
    border-radius: 10px;
    min-height: 10px;
    padding: 8px;
    position: relative;
    &-right{
      align-self: flex-end;
      & .ball__year{
        left: -48px;
      }
    }
    &-left{
      align-self: flex-start;
      & .ball__year{
        right: -51px;
      }
    }
    &__year{
      text-align: center;
      width: 40px;
      position: absolute;
      font-weight: bold;
      background-color: #fff;
      border-radius: 100%;
      border: 2px solid #2c98f0;
      padding: 3px 2px;
      top: 50%;
      bottom: 50%;
      margin-top: -20px;
      height: fit-content;
      color: rgba(0, 0, 0, .7);
    }
    &__skill{
      & > img{
        margin-right: 8px;
        &:last-of-type{
          margin-right: 0;
        }
      }
    }
    & > p{
      color: rgba(0, 0, 0, .7);
      font-size: calc(.1em + 1vw);
      line-height: calc(.8em + 1vw);
    }
    & > h3 {
      margin: 8px 0;
    }

    & > ul {
      & > li {
        color: rgba(0, 0, 0, .7);
        font-size: calc(.1em + 1vw);
        line-height: calc(.8em + 1vw);
        margin-left: 20px;
      }
    }
  }
</style>



