<template>
  <div class="page content">
    <ve-input
      v-model="val1"
      label=" "
      icon="&#xe8b4;"
      colorIcon="#000"
      desc="asdasd sa d as das d sa dsa d sa dadsadas das dsa d ad sa das"
      :v="$v.val1"
      errorMessage="test"
      mask="*{1,20}[.*{1,20}][.*{1,20}][.*{1,20}]@*{1,20}[.*{2,6}][.*{1,3}]"
    ></ve-input>
    <br>
    <ve-autocomplete v-model="val2" :list="arr" label=""
                     :fetchList="autocomplete"
    ></ve-autocomplete>
    <br>
    <ve-select v-model="select" :list="arr"></ve-select>
    <br>
    <ve-select v-model="arrSelect" :list="arr" multiSelect></ve-select>
    <br>
    <ve-radio v-model="radio" label="label"></ve-radio>
    <ve-radio v-model="radio" label="label1"></ve-radio>
    <br><br>
    <ve-radio-group v-model="radioGroup" style="margin-bottom: 16px">
      <ve-radio label="label1"/>
      <ve-radio label="label2"/>
      <ve-radio label="label3" disabled/>
    </ve-radio-group>
    <br><br>
    <ve-checkbox v-model="checkBoxFalse" label="label1"/>
    <ve-checkbox v-model="checkBoxTrue" label="label2" disabled/>
    <ve-checkbox v-model="checkBoxFalse" label="label3" disabled/>
    <br><br>
    <ve-checkbox-group v-model="checkboxGroup" style="margin-bottom: 16px">
      <ve-checkbox label="label1"/>
      <ve-checkbox label="label2"/>
      <ve-checkbox label="label3"/>
    </ve-checkbox-group>
    <br><br>
    <ve-switch v-model="switchValue" label="label1"/>
    <ve-switch v-model="switchValue" label="label2" disabled/>
  </div>
</template>

<script>

  import {VeInput, VeAutocomplete, VeSelect, VeRadio, VeRadioGroup, VeCheckbox, VeCheckboxGroup, VeSwitch} from 'vue-elements-ui'
  import { validationMixin } from 'vuelidate'
  import { required, maxLength, email } from 'vuelidate/lib/validators'
  export default {
    name: 'Experience',
    components:{VeInput, VeAutocomplete, VeSelect, VeRadio, VeRadioGroup, VeCheckbox, VeCheckboxGroup, VeSwitch},
    mixins: [validationMixin],
    data () {
      return {
        val1: '',
        val2: '',
        arr: [
          {id: 1, value: 'test1'},
          {id: 2, value: 'test213'},
          {id: 3, value: 'test312'},
          {id: 4, value: 'test412'},
          {id: 5, value: 'test5'},
          {id: 6, value: 'test6'},
        ],
        select: {},
        arrSelect: [],
        radioGroup: 'label3',
        radio: 'label',
        checkBoxTrue: true,
        checkBoxFalse: false,
        checkboxGroup: [],
        switchValue: false,
      }
    },
    validations: {
      val1: { required, email },
    },
    methods:{
      autocomplete(query, cb){
        let Items = this.arr;
        if(query){
          Items = Items.filter((item) =>
            item.value.toLowerCase().indexOf(query.toLowerCase()) + 1);
        }
        setTimeout(() => {cb(Items);}, 2000)

      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
</style>



